import axios from 'axios'

const http = config => {
	const instance = axios.create({
		baseURL: "http://0.0.0.0:3000",
		timeout: 10000
	})
	
	instance.interceptors.request.use(req => {
		return req
	})
	
	instance.interceptors.response.use(res => {
		return res.data
	})
	
	return instance(config)
}

export default http
