import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import MarkdownIt from 'markdown-it'

import Highlight from 'assets/plugin/Highlight'
import blogConfig from 'assets/script/blog-config'
import { toGMT } from 'assets/script/lib'
import get from '@/network/get'
import { 
	ARTICLES_DATA, 
	ARTICLES_INFO,
	BLOG_CONFIG,
	IS_HIDE_NAV,
	IS_SHOW_FLOATINGBUTTON,
	REQUEST_PARAMS,
	SCROLL_Y
} from 'store/mutation-types'

Vue.config.productionTip = false
Vue.use(Highlight)

Vue.prototype.rmd = new MarkdownIt()
Vue.prototype.$get = get

new Vue({
  router,
	store,
  render: h => h(App),
	created() {
		
		this.$store.commit(BLOG_CONFIG, blogConfig)
		this.$get('/articles', {start: 0, limit: 3}).then(this._initData)
		window.onscroll = this.onScroll()
		
	},
	methods: {
		
		_initData(data) {
			
			const map = new Map()
			
			data.articles.forEach(v => {
				v.data.date = toGMT(v.data.date)
				this.$store.commit(ARTICLES_INFO, v.data)
				map.set(v.data.title, v)
			})
			
			this.$store.commit(REQUEST_PARAMS, {
				start: data.start + data.count, 
				total: data.total,
			})
			this.$store.commit(ARTICLES_DATA, map)
			
		},
		
		// 节流
		onScroll() {
			
			let preScrollY = 0
			let timer = null
			
			return () => {
				
				if (timer) return
				
				timer = setTimeout(() => {
					
					const tmpPos 					= window.pageYOffset,
						isBreakThreshold 		= tmpPos > this.$store.state.threshold,
						isAdvance 					= tmpPos >this.$store.state.scrollY
					
					isAdvance ?
						this.$store.commit(IS_HIDE_NAV, true) : this.$store.commit(IS_HIDE_NAV, false)
					
					isBreakThreshold ?
						this.$store.commit(IS_SHOW_FLOATINGBUTTON, true) : this.$store.commit(IS_SHOW_FLOATINGBUTTON, false)

					this.$store.commit(SCROLL_Y, tmpPos)
					
					timer = null
					
				}, 60)
			}
		}
	}
}).$mount('#app')
