export default {
	articles(state) {
		return state.articlesData.articles
	},
	articlesData(state) {
		return state.articlesData
	},
	articlesInfo(state) {
		return state.articlesInfo
	},
	articlesNumber(state) {
		return state.articlesData.size
	},
	article(state) {
		return title => state.articlesData.get(title)
	}
}
