import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import getters from './getters'

Vue.use(Vuex)

const state = {
	blogConfig: {},
	info: {
		articles: 0,
		categories: [],
		tags: []
	},
	threshold: 300,
	isHideNav: false,
	isShowFlotingButton: false,
	articlesData: null,
	articlesInfo: [],
	requestParams: {
		start: 0,
		total: 0
	},
	scrollY: 0,
	pageHeight: 0
}

const store = new Vuex.Store({
	state,
	mutations,
	getters
})

export default store

