<template>
	<p class="article-cover-info">
		
		<span class="article-cover-item">
			<i class="iconfont icon-text"></i>
			<i class="article-cover-info-key">文章字数</i>
			<b class="article-cover-info-value">{{ word }}</b>
		</span>
		
		<span class="article-cover-item">
			<i class="iconfont icon-timer"></i>
			<i class="article-cover-info-key">阅读时长</i>
			<b class="article-cover-info-value">{{ timer }}</b>
		</span>
		
	</p>
</template>

<script>
	
	export default {
		name: 'ArticleCoverInfo',
		props: {
			info: {
				type: Object,
				required: true
			}
		},
		computed: {
			word() {
				return (~~(this.info.wordCount / 100)) / 10 + ' k'
			},
			timer() {
				return Math.round(this.info.wordCount / 600) + ' mins'
			}
		}
	}
	
</script>

<style lang="stylus" scoped>
	
	.article-cover-info
		margin-top 2rem
		font-size 1.6rem
		font-weight bold
		
		.article-cover-item
			color #fff
			margin-right 1.8rem
			
			.iconfont,
			.iconfont ~ i
				margin-right 0.6rem
				color #7a7a8c
				
			.iconfont
				font-size 1.8rem
	
</style>
