import Vue from 'vue'
import Hljs from 'highlight.js'
import 'highlight.js/styles/atom-one-dark.css'

export default {
	install(Vue, options) {
		Vue.directive('highlight', el => {
			el.querySelectorAll('pre > code').forEach(block => {
				block.classList.add('code')
				Hljs.highlightBlock(block)
			})
		})
	}
}
