<template>
	<p class="article-cover-top">
		<a href="" class="article-cover-cate">
			<b class="article-cover-cate-symbol">「 </b>
			<router-link
				:to="{path: '/filter', query: {name: 'Category', filter: cate}}"
			>{{ cate }}</router-link>
			<b class="article-cover-cate-symbol"> 」</b>
		</a>
		<time class="article-cover-date">
			<span>{{ month }}</span>
			<span> {{ day }}, </span>
			<span> {{ date.getFullYear() }}</span>
		</time>
	</p>
</template>

<script>
	
	export default {
		name: 'ArticleCoverTop',
		props: {
			date: {
				type: Date,
				required: true
			},
			cate: {
				type: String,
				required: true
			}
		},
		data() {
			return {
				dateMap: {
					1: '一月',
					2: '二月',
					3: '三月',
					4: '四月',
					5: '五月',
					6: '六月',
					7: '七月',
					8: '八月',
					9: '九月',
					10: '十月',
					11: '十一月',
					12: '十二月'
				}
			}
		},
		computed: {
			month() {
				return this.dateMap[this.date.getMonth() + 1]
			},
			day() {
				const day = this.date.getDate()
				return day < 10 ? '0' + day : day
			}
		}
	}
	
</script>

<style lang="stylus" scoped>
	
	.article-cover-top
		color #7a7a8c
		font-weight bold
	
		.article-cover-cate
			transition .1s
			text-transform uppercase
			color #fff
		
			&:hover
				color #0ed2f7
				
			.article-cover-cate-symbol
				color #0ed2f7
	
</style>
