<template>
	<div class="loadmore" v-if="isLoadedAllArticle">
		<a
			href="javascript:;"
			class="loadmore-link"
			@click.prevent="loadMore">加载更多</a>
	</div>
</template>

<script>
	
	import { 
		REQUEST_PARAMS_START,
		ADD_ARTICLE_DATA,
		UPDATE_PAGE_HEIGHT
	} from '@/store/mutation-types'
	
	import { toGMT } from '@/assets/script/lib'
	
	export default {
		name: 'LoadMore',
		methods: {
			loadMore() {
				this.$get('/articles', { 
					start: this.$store.state.requestParams.start, limit: 3}).then(data => {
					
					this.$store.commit(REQUEST_PARAMS_START, data.start + data.count)
					
					data.articles.forEach(art => {
						
						art.data.date = toGMT(art.data.date)
						
						this.$store.commit(ADD_ARTICLE_DATA, {
							key: art.data.title,
							value: art
						})
						
						this.$nextTick(() => this.$store.commit(UPDATE_PAGE_HEIGHT))
					})
				})
			}
		},
		computed: {
			isLoadedAllArticle() {
				return (this.$store.state.requestParams.start !== this.$store.state.requestParams.total)
			}
		}
	}
	
</script>

<style lang="stylus" scoped>
	
	.loadmore
		margin 6rem auto
		text-align center
		
		.loadmore-link
			border 5px solid rgba(255,255,255,.46)
			padding 1rem 4rem
			border-radius 1.4rem
			background: linear-gradient(to right, var(--primary), var(--secondary), var(--dark))
			background-clip text
			color transparent
	
</style>
