<template>
	<transition name="show">
		<div
			class="floating-button"
			v-if="$store.state.isShowFlotingButton">
			<div class="fbt-content">
				<div class="fbt-progress">{{ progress }}</div>
				<div class="fbt-top" @click="top">Top</div>
			</div>
		</div>
	</transition>
</template>

<script>
	
	export default {
		name: 'FloatingButton',
		props: {
			pageHeight: {
				type: Number,
				required: true
			},
			scrollHeight: {
				type: Number,
				required: true
			},
		},
		data() {
			return {
				clientHeight: 0
			}
		},
		created() {
			this.clientHeight = document.body.clientHeight
		},
		computed: {
			progress() {
				return (Math.round(this.pageScrolledHeight / this.pageHeight * 100) + '%')
			},
			pageScrolledHeight() {
				return this.scrollHeight + this.clientHeight
			}
		},
		methods: {
			top() { window.scrollTo(0, 0) }
		}
	}
	
</script>

<style lang="stylus" scoped>
	
	@import '~assets/stylus/variable.styl'
	
	.show-enter-active,
	.show-leave-active
		transform .4s
		
	.show-enter,
	.show-leave-to
		transform translateX(200%)
		
	.floating-button
		cursor pointer
		position fixed
		right 40px
		bottom 80px
		font-size 1.6rem
		width 50px
		height 50px
		line-height 50px
		background-color #fff
		border-radius 50%
		box-shadow 0 0 12px rgba(0, 0, 0, .1)
		transition .4s
		text-align center
		overflow hidden
		
		&:hover
			.fbt-content
				transform translateY(-50%)
				
		.fbt-content
			transition .2s
		
			.fbt-progress,
			.fbt-top
				width 100%
				height 100%

</style>
