<template>
	<div class="article-cover">
		<div class="article-cover-mask"
			:style="{backgroundImage: coverUrl}"></div>
		<div class="article-cover-content" v-if="isActive" >
			<article-cover-top
				:cate="info.category"
				:date="info.date" />
			<router-link
				:to="{path: '/article', query: { title: info.title }}"
				class="article-cover-title">{{ info.title }}</router-link>
			<article-cover-info :info="info" />
			<article-cover-tag :tags="info.tags" />
		</div>
	</div>
</template>

<script>
	
import ArticleCoverTag from './components/ArticleCoverTag'
import ArticleCoverInfo from './components/ArticleCoverInfo'
import ArticleCoverTop from './components/ArticleCoverTop'

export default {
	name: 'ArticleCover',
	props: {
		info: {
			type: Object,
			required: true
		}
	},
	data() {
		return {
			defaultUrl: 'http://cdn.eqingchen.top/blog/image/articleCover.default.jpg',
			isActive: true
		}
	},
	components: {
		ArticleCoverTag,
		ArticleCoverInfo,
		ArticleCoverTop
	},
	computed: {
		coverUrl() {
			return `url(${ this.info.cover || this.defaultUrl })`
		}
	},
	activated() {
		this.isActive = true
	},
	deactivated() {
		this.isActive = false
	}
}
	
</script>

<style lang="stylus" scoped>
	
	.article-cover
		width 100%
		height 100%
		overflow hidden
		position relative
		display flex
		align-items center
		justify-content center
		background-color #00030B
			
		.article-cover-mask
			position absolute
			top 0
			left 0
			right 0
			bottom 0
			background-size cover
			
			&::after
				content ""
				position absolute
				width 100%
				height 100%
				background-image radial-gradient(ellipse closest-side, rgba(0, 0, 0, .65), #00030b)
			
		.article-cover-content
			width 120rem
			position absolute
			animation cover 1s
				
			.article-cover-title
				font-weight bold
				display inline-block
				background linear-gradient(to right,
					rgb(178, 254, 250),
					rgb(14, 210, 247),
					rgb(0, 82, 212))
				background-clip text
				-webkit-background-clip text
				-moz-background-clip text
				box-decoration-break clone
				-webkit-box-decoration-break clone
				-moz-box-decoration-break clone
				color transparent
				font-size 5.6rem
				margin-top 2rem
				cursor pointer
				
		@media screen and (max-width: 400px)
			.article-cover-content
				width 90%
				margin 0 auto
				
				.article-cover-title
					font-size 3.4rem
				
				
	@keyframes cover
		0%
			opacity 0
			transform translateY(-100px)
	
		
</style>
