import {
	ARTICLES_DATA,
	ARTICLES_INFO,
	BLOG_CONFIG,
	INFO,
	IS_HIDE_NAV,
	IS_SHOW_FLOATINGBUTTON,
	ADD_ARTICLE_DATA,
	REQUEST_PARAMS,
	REQUEST_PARAMS_START,
	REQUEST_PARAMS_TOTAL,
	SCROLL_Y,
	UPDATE_PAGE_HEIGHT,
	THRESHOLD
} from './mutation-types'

import Vue from 'vue'

export default {

	// 设置当前页面的高度
	[UPDATE_PAGE_HEIGHT](state) {
		// Vue.set(state, 'pageHeight', document.body.scrollHeight)
		state.pageHeight = document.body.scrollHeight
	},
	
	// 滚动条当前滚动位置
	[SCROLL_Y](state, posY) {
		state.scrollY = posY
	},
	
	// 网络请求的参数
	[REQUEST_PARAMS_TOTAL](state, value) {
		Vue.set(state.requestParams, 'total', value)
	},
	
	// 网络请求的参数
	[REQUEST_PARAMS_START](state, value) {
		Vue.set(state.requestParams, 'start', value)
	},
	
	// // 网络请求的参数对象
	[REQUEST_PARAMS](state, data) {
		state.requestParams = data
	},
	
	// 所有文章数据
	[ADD_ARTICLE_DATA](state, article) {
		this.commit(ARTICLES_INFO, article.value.data)
		state.articlesData.set(article.key, article.value)
	},
	
	// 所有文章的信息
	[ARTICLES_INFO](state, info) {
		state.articlesInfo.push(info)
	},
	
	// 更换 articleData 指向
	[ARTICLES_DATA](state, data) {
		state.articlesData = data
	},
	
	// 网站配置信息
	[BLOG_CONFIG](state, data) {
		state.blogConfig = data
	},
	
	// 分类，标签，文章 的总数
	[INFO](state, data) {
		state.info = data
	},
	
	// 阈值: 超过这个值则显示浮动按钮(FloatingButton)
	[THRESHOLD](state, data) {
		if (state.threshold === data) return
		
		state.threshold = data
	},
	
	// 是否显示导航栏
	[IS_HIDE_NAV](state, data) {
		if (state.isHideNav === data) return
		
		state.isHideNav = data
	},
	
	// 是否显示浮动按钮(FloatingButton)
	[IS_SHOW_FLOATINGBUTTON](state, data) {
		if (state.isShowFlotingButton === data) return
		
		state.isShowFlotingButton = data
	}
}
