<template>
	<div class="home">
		<article-list />
		<load-more />
		<copyright />
		<floating-button
			v-if="isComplete"
			:pageHeight="$store.state.pageHeight"
			:scrollHeight="$store.state.scrollY" />
	</div>
</template>

<script>

	import ArticleList from '@/components/ArticleList/ArticleList'
	import FloatingButton from 'components/FloatingButton/FloatingButton'
	import LoadMore from '@/components/LoadMore/LoadMore'
	import Copyright from '@/components/Copyright/Copyright'
	
	import { UPDATE_PAGE_HEIGHT } from '@/store/mutation-types'
	
	export default {
		name: "Home",
		data() {
			return {
				isListComplete: false,
				isComplete: false,
				pageHeight: 0,
			}
		},
		components: {
			ArticleList,
			FloatingButton,
			LoadMore,
			Copyright
		},
		mounted() {
			this._initData()
		},
		methods: {
			_initData() {
				// 保证能后获取到正确出高度
				setTimeout(() => {
					this.$store.commit(UPDATE_PAGE_HEIGHT)
					this.isComplete = true
				}, 60)
			}
		}
	}
	
</script>

<style lang="stylus" scoped>

	.home
		position absolute
		width 100%
		background-color #00030B
		padding-top 5.84rem
	
</style>
