import Vue from 'vue'
import VueRouter from 'vue-router'

import Home from '@/views/Home/Home'

Vue.use(VueRouter)

const routes = [
	{
		path: '/',
		name: 'Home',
		component: Home,
		meta: {
			index: 0
		}
	},
	{
		path: '/article',
		name: 'Article',
		component: () => import('@/views/Article/Article'),
		meta: {
			index: 1
		}
	},
	{
		path: '/archive',
		name: 'Archive',
		component: () => import('@/views/Archive/Archive'),
		meta: {
			index: 2
		}
	},
	{
		path: '/tool',
		name: 'Tool',
		component: () => import('@/views/Tool/Tool'),
		meta: {
			index: 3
		}
	},
	{
		path: '/about',
		name: 'About',
		component: () => import('@/views/About/About'),
		meta: {
			index: 4
		}
	},
	{
		path: '/category',
		name: 'Category',
		component: () => import('@/views/Category/Category'),
		meta: {
			index: 5
		}
	},
	{
		path: '/tag',
		name: 'Tag',
		component: () => import('@/views/Tag/Tag'),
		meta: {
			index: 6
		}
	},
	{
		path: '/filter',
		name: 'Filter',
		component: () => import('@/views/FilterPage/FilterPage'),
		meta: {
			index: 7
		}
	}
]

const router = new VueRouter({
	mode: 'history',
	routes,
	scrollBehavior(to, from) {
		return {
			x: 0,
			y: 0
		}
	}
})

export default router
